.dragAndDrop-component {
    .dragAndDrop-inner {

        .dragAndDrop-container {
            vertical-align: top;

            &.scale {
                transform-origin: 0 0%;
                -ms-transform-origin: 0 0%;
                -moz-transform-origin: 0 0%;
                -o-transform-origin: 0 0%;
                -webkit-transform-origin: 0 0%;
            }

            &.draggables-container {
                .draggable-item-wrapper {
                    display: block;
                    border: 1px dotted #5c5c5b;
                    border-radius: 5px;
                    margin: 15px 0px 5px 12px;
                    float: left;

                    .draggable-item {
                        display: block;
                        -webkit-user-select: none;
                        -moz-user-select: none;
                        -ms-user-select: none;
                        user-select: none;
                        text-decoration: none;
                        color: @black;

                        .no-touch &:hover {
                            cursor: pointer;
                        }
                    }
                }
            }

            &.droppables-container {
                display: inline-block;
                float: left;
                margin: 40px 0 5px;
               // height: 243px;
               // width: 470px;
                .droppable-item {
                    display: block;
                    position: absolute;
                    margin: 0 auto 2px 5px;
                    border-width: 1px;
                    border-style: dashed;
                    border-radius: 5px;
                }
            }

            .dragAndDrop-item-container {
                position: relative;
                float: left;

                .dragAndDrop-item-title {
                    font-size: 20px;
                    font-weight: 600;
                    line-height: 35px;
                    height: 35px;
                    margin: 0 0 5px 5px;
                    padding: 5px 0 5px 0;
                    text-align: center;
                    border: 1px solid #dcddde;
                    background-color: @white;
                    font-color: #102a3e;
                }
               /* &.droppable-item-container{
                    position: absolute;
                }*/
            }
        }

        .dragAndDrop-dummy {
            display: block;
            position: fixed;
            background-color: #4b4e4f;
            z-index: 1;
            top: 0px;
            left: 0;
            display: block;
            cursor: pointer;
            color: #fff;
            text-align: center;
            border-radius: 5px;
            padding: 5px 0 0;            
        }
    }

    .dragAndDrop-widget {
        position: relative;
        display: inline-block;

        &.show-user-answer {
            .incorrect {
                .draggable-item-incorrect-icon{
                    display: block !important;
                  }
                  .draggable-item-correct-icon{
                    display: none;
                  }
            }

            .correct {
                .draggable-item-correct-icon{
                    display: block !important;
                  }
                  .draggable-item-incorrect-icon{
                    display: none;
                  }
            }
        }

        &.show-correct-answer {
            .droppable-item {
                border-style: solid;
                border-color: @green;
            }
            .correct {
                .draggable-item-correct-icon{
                  display: block!important;
                }
              }
        }

        &.disabled {
            .draggable-item {
                pointer-events: none;
                .no-touch &:hover {
                    cursor: default;
                }
            }
        }
    }
      a{
        display: block;
        text-decoration: none !important;
        color: #ffffff !important;
        background: #68d2b3;
        padding: 10.5px;
        text-align: center;
        border-radius: 5px
  }
}
