.stacklist-items {
	position: relative;
	margin-bottom: -10px;
}

.stacklist-item {
	position: relative;
	padding: 0px;
	margin-bottom: 10px;
	background-color: @primary-color;
	color: @primary-color-inverted;
	overflow: hidden;

    &.show {
		position: static !important;
		border-bottom:5px solid;
		border-color:@light-green;
    }
}
.stacklist-button {
	display: none;
	position: absolute;
	top: 0;
	width: 100%;
	text-align: center;
	margin-top: 20px;

	button {
		width: 100%;
		padding: 0;
		margin: 0;
    	height: 36px;
		img{
			/*background: url(assets/baseline-arrow_back.png) no-repeat;*/
			transform: rotate(270deg);
		}
	}
}

.container{
	background-color:@light-green;
	width:100%;
	display:flex;

}
.fixed{
	font-size: 72px;
	line-height: 100px;
	min-width:100px;
	width: 100px;
	text-align: center;
}
.flex-item{
	padding: 10px 22px;
	background-color:#454545;
	flex-grow: 1;
	line-height: 20px;
}

.icon-down{
	background: url(assets/baseline-arrow_back.png) no-repeat;
	transform: rotate(270deg);
}