.multiDragAndDrop-component {
  .multiDragAndDrop-inner {
    .multiDragAndDrop-draggables-container {
      display: inline-block;
      width: 100%;
      font-size: 15px;
      vertical-align: top;
  	  @media (max-width: 768px){
    		width: 100%;
    		margin: auto;
	    }
      .multiDragAndDrop-item-draggable-wrapper {
        display: block;
        border:1px dotted #5c5c5b;
        border-radius:5px;
        margin: 15px 0px 5px 12px;
        float: left;

    @media (max-width: 520px){
          margin: 5px 10px 5px 0px;
    }


      }
    }

    .multiDragAndDrop-droppables-container {
      display: inline-block;
      width: 100%;
      vertical-align: top;

	  .multiDragAndDrop-item-background{
  		width: 26.5%;
  		float: left;
  		margin: 30px 15px;
  		border: dashed 1px #5c5c5b;
      border-style: dashed;
     @media (max-width: 520px){
      background-image: none;
      width: 37% !important;
      float: left;
      margin: -3% !important;
      border: dashed 2px #5c5c5b;
      border-style: dashed;
      transform: scale(0.7);
      min-height: 300px;
    }

  }

    .multiDragAndDrop-item-draggable {
    background: #68d2b3 !important; // color after drop in droppable zone
    margin: 4px !important;
    text-align: center;

    display: block;
    text-decoration: none !important;
    color: #ffffff !important;
    -webkit-user-select:none;
    -moz-user-select:none;
    -ms-user-select:none;
    -o-user-select:none;
    user-select:none;


 @media (max-width: 520px){
  color: #ffffff !important;
  font-size: 12px;
  line-height: 27px;
 }

}

      &:hover {
        cursor: pointer;
      }
    }

    .multiDragAndDrop-dummy {
      display: block;
      position: fixed;
      background-color: #5c5c5b; // color when we drad item
      color:#fff;
      border-radius:5px;
      text-align:center;
      top: 0;
      left: 0;
      z-index: 1;
      cursor: pointer;
      padding: 5px 0 0;
    }
  }

  .multiDragAndDrop-widget {

    &.show-user-answer {
      .incorrect {
        .multiDragAndDrop-incorrect-icon{
          display: block !important;
        }
        .multiDragAndDrop-correct-icon{
          display: none;
        }
       // border-style: solid;
        //border-color: #f26c4f;
      }
      .correct {
        .multiDragAndDrop-correct-icon{
          display: block !important;
        }
        .multiDragAndDrop-incorrect-icon{
          display: none;
        }
        //border-style: solid;
        //border-color: rgb(1, 131, 55);
      }
    }

    &.show-correct-answer {
      .multiDragAndDrop-item-droppable {
        border-style: solid;
        border-color: rgb(1, 131, 55);
      }
      .correct {
        .multiDragAndDrop-incorrect-icon{
          display: none !important;
        }
        .multiDragAndDrop-correct-icon{
          display: block!important;
        }
      }
    }

    &.disabled {
      .multiDragAndDrop-item-draggable {
        &:hover {
          cursor: default;
        }
      }
    }
  }


  .multiDragAndDrop-item-droppable {

    width: 100% !important;
    text-align: center !important;    
    color: #5c5c5b;

    @media (max-width: 520px){ 
		width: 100% !important;
		text-align: center;
		font-weight: bold;
		font-size: 18px;
		margin: 0% !important;
		background: transparent;
		color: #5c5c5b;
  }

  }


  a{
    display: block;
      text-decoration: none !important;
      color: #ffffff !important;
      background: #57BA9E; // color of dragged item
      padding: 10.5px;
      text-align: center;
      border-radius: 5px;
  }




}
.notify .notify-popup-title {
  margin-bottom: 0px !important;
  padding-right: 0px !important;
}