.article.article-noheader .article-inner {
	padding-top: 0;
	background-image: none;
	background-position: 0% 0%;
	background-repeat: repeat;
}

.results-retry-button {
	margin: @button-margin-top auto 0;
	text-align: center;
	border-bottom: 1px solid #bbbbbb;
    padding-bottom: 20px;
	//width: 22%; 
	@media all and (max-width: @device-width-medium) {
		//width: 40%;
	}   
}
.results-retry-button{
	button{
		background: #976eab;
		border-radius: 3px;
		padding: 6px 10px !important;
		text-transform: uppercase;
		font-size: 13px;
		font-weight: 500;
		letter-spacing: 1px;
	}
}

.assessmentResults-component.visibility-hidden {
	display: none;
}

.result-canvas{
	width: 500px;
	height: 500px;
    display: block;
	background-color: transparent;
	margin: 0 auto;
}
.result-bottom-text{
	text-align: center;
    width: 45%;
    margin: 0 auto;
	font-size: 14px;
	@media all and (max-width: @device-width-medium) {
		width: 40%;
	}  
	@media all and (max-width: @device-width-small) {
		width: 90%;
	} 
}